.feedback {
	position: relative;

	display: block;

	grid-column: 1 / 4;
	@include gl(#342f2e,#504641);
	&::before {
		position: absolute;
		z-index: 1;
		//z-index должен подойти
		top: 0;
		left: 0;

		display: block;

		width: 100%;
		height: 100%;

		content: '';

		opacity: .3;
		background-color: transparent;
		background-image: url(data:image/png;base64,); }
	&::after {
		content: ' '; }
	&__contacts {
		z-index: 2;

		display: flex;
		flex-wrap: wrap;

		grid-row: 1 / 2;
		grid-column: 1 / 13;
		&:first-child {
			margin-top: 0;
 }			//contacts__item
		&_item {
			display: flex;
			align-items: center;
			justify-content: center;

			width: 100%;
			margin-top: 50px;
			//выравнивание иконок, заебал fontawesome -____-
			margin-right: 15px;

			color: #c7b299;
			&:first-child > :first-child {
				padding: 15px 5.5px 0 0; }
			&:nth-child(2) > :first-child {
				padding: 16.5px 0 0 2.5px; }
			&:last-child > :first-child {
				padding: 15px 3px 0 0; }
			&:last-child {
				margin-right: 45px; }
			@include media-breakpoint-up(md) {
				width: 33.3%;
				margin: 0;
				&:last-child {
					margin-right: 0; } } }

		&_icon {
			display: flex;
			align-items: center;
			justify-content: center;

			width: 71px;
			height: 63px;
			margin: 0 20px 0 0;

			background-image: url(data:image/svg+xml;base64,);

			font-size: 1.375rem; } }
	&__social {
		&_link {
			margin-left: 15px;

			cursor: pointer;

			font-size: 1.25em; }
		&_icon {
			transition: color .15s linear;

			color: #c7b299;
			&:hover {
				color: #fff; } } }

	&__notification {
		display: none;
		grid-column: 1 / 13;
		grid-row: 2 / 3;
		padding: .5em 1em;
		border-radius: 2px;
		border: 1px solid;
		margin: 40px 0 0 0;
		color: #fff;
		@include media-breakpoint-up(md) {
			margin: 40px 15px 0 15px; }
		&_success {
			background-color: rgba(105, 205, 130, .2);
			border-color: rgba(105, 205, 130, .7); }
		&_error {
			background-color: rgba(178, 34, 34, .2);
			border-color: rgba(178, 34, 34, .7); } }
	&__form {
		z-index: 2;

		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		margin-top: 70px;

		grid-row: 3 / 4;
		grid-column: 1 / 13;
		&_input {
			width: 100%;
			height: 100%;
			padding: 0 20px 0 20px;

			transition: border-color .15s linear;

			color: #c7b299;
			border: 1px solid #c7b299;
			background-color: transparent;

			font-size: .875em;
			font-weight: 300;
			&::placeholder {
				transition: opacity .15s linear;

				opacity: 1;
				color: #c7b299;

				font-size: .875em;
				font-weight: 300; }
			&:focus {
				color: #fff;
				border: 2px solid #fff;
				&::placeholder {
					opacity: 0;
					color: #fff; } }
			&_wrap {
				position: relative;

				justify-content: center;

				width: 100%;
				height: 40px;
				margin-top: 40px;

				@include media-breakpoint-up(md) {
					width: calc(33.3% - 30px);
					margin: 0 0 0 30px;
					&:first-child {
						margin: 0; } } }
			&_icon {
				position: absolute;
				top: 0;
				right: 21px;

				transition: color .15s linear;

				color: #c7b299;

				font-size: .875rem;
				line-height: 2.85; } }
		&_message {
			width: 100%;
			height: 155px;
			padding: 15px 20px 15px 20px;

			resize: none;
			transition: border-color .15s linear;

			color: #c7b299;
			border: none;
			border: 1px solid #c7b299;
			background-color: transparent;

			font-size: .875em;
			font-weight: 300;
			&::placeholder {
				transition: opacity .15s linear;

				opacity: 1;
				color: #c7b299;

				font-size: .875em;
				font-weight: 300; }
			&:focus {
				color: #fff;
				border: 2px solid #fff;
				&::placeholder {
					opacity: 0;
					color: #fff; }
				.feedback__form_message_icon {
					display: none; } }
			&_wrap {
				position: relative;

				width: 100%;
				margin: 40px 0 0 0;

				@include media-breakpoint-up(md) {
					width: calc(100% - 30px);
					margin: 30px 0 0 0; } }
			&_icon {
				position: absolute;
				top: 15px;
				right: 33px;

				width: 0;
				height: 0;

				transition: color .15s linear;

				color: #c7b299;

				font-size: .875rem; } }

		&_button {
			margin-top: 40px;

			@include media-breakpoint-up(md) {
				margin-top: 25px; } } } }
