.promo {
	position: relative;
	z-index: 1;

	height: 910px;

	background-color: #554e4d;
	background-image: url(../img/slide1.jpg);
	background-position: center center;
	background-size: cover;

	grid-row: 1 / 3;
	grid-column: 1 / 4;
	&::before {
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;

		display: block;

		width: 100%;
		height: 100%;

		content: '';

		background-color: rgba(88, 83, 81, .7); }
	&::after {
		position: absolute;
		z-index: 3;
		top: 0;
		left: 0;

		display: block;

		width: 100%;
		height: 100%;

		content: '';

		opacity: 1;
		background-image: url(data:image/pngbase64,); }
	&__content {
		position: absolute;
		z-index: 4;
		top: 0;
		left: 0;

		display: flex;
		align-content: center;
		flex-wrap: wrap;
		justify-content: center;

		width: 100%;
		height: 100%;

		grid-column: 1 / 13;
		&_title {
			order: 1;

			width: 100%;
			margin-top: 10px;

			text-align: center;

			color: #fff;

			font-size: 2em;
			font-weight: 400;

			@include media-breakpoint-up(sm) {
				font-size: 2.75em;
				font-weight: 500; } }
		&_text {
			order: 2;

			width: 100%;
			padding: 0;

			text-align: center;

			font-size: .875em;

			@include media-breakpoint-up(sm) {
				padding: 0 10%; }
			@include media-breakpoint-up(md) {
				display: block;

				padding: 0 15%; } }
		&_button {
			order: 3;

			height: 45px;
			margin-top: 25px;

			@include media-breakpoint-up(md) {
				margin-top: 35px; } } } }
